h1,h2,h3,h4,h5,h6{
	padding: 0;
	margin: 0;
}
a{
	color: $primary_black;
	text-decoration: none;
}
a:hover{
	text-decoration: none;
}
.flexColumn{
	@include flex-direction(column);
}
.flexRow{
	@include flex-direction(row);
}
.flexBox{
	@include flexbox();
}
.flex100{
	@include flex(1 0 0);
}
.flex110{
	@include flex(1 1 0);
}
.flex000{
	@include flex(0 0 0);
}
.starter-template {
    padding: 3rem 1.5rem;
    text-align: center;
}
.topbar{
    padding: 5px 1rem 5px;
    font-size: 0.875em;
    background:#bf2d26;
    @include boxShadow(0 1px 3px rgba(0, 0, 0, 0.28));
    position: relative;
    z-index: 999;
    .choose-location{
        cursor: pointer;
        float: right;
        color: #FFF;
        .choose{
            margin-left: 5px;
            color: #333;
            font-size: 0.750rem;
            margin-top: 3px;
        }
    }
    .left-nav{
        float: right;
        li{
            float: left;
            a{
                display: block;
                text-transform: uppercase;
                padding: 3px 10px;
                color: #FFF;
                &.cart{
                    padding: 2px 9px;
                }
            }
            &.active{
                a{
                    font-weight: 700;
                    color: #FFF;
                    background: #921610;
                }
            }
        }
    }
}
nav.navbar{
    padding: 5px 1rem;
    .navbar-brand{
        padding: 0;
    }
    .navbar-nav{
        li{
            a{
                display: block;
                text-transform: uppercase;
                font-size: 0.875em;
                font-weight: 700;
            }
        }
    }
    .right-nav{
        float: right;
        li{
            float: left;
            margin-left: 20px;
            .myaccount{
                text-transform: uppercase;
                font-size: 0.875rem;
                color: #000;
            }
            .cart{
                span.badge{
                    position: relative;
                    top: -2px;
                }
            }
            .choose-location{
                cursor: pointer;
            }
        }
    }
    .primary-search{
        width: 100%;
        max-width: 540px;
        position: relative;
        input[type="search"]{
            padding-right: 30px;
        }
        .fa-search{
            position: absolute;
            right: 12px;
            top: 11px;
        }
        .custom-select{
            border-radius: 0.25rem 0 0 0.25rem;
            background-color: #EEE;
        }
    }
}
.banner-carousel{
    padding: 0;
    .rightRs, .leftRs {
        position: absolute;
        top: 50%;
        font-size: 3em;
        margin-top: -36px;
        left: 30px;
        i{
            color: #FFF;
            text-shadow: 1px 1px 3px rgba(0,0,0,0.5);
        }
    }
    .rightRs{
        right: 30px;
        left: initial;
    }
    .item{
        min-height: 500px;
        background-size: cover;
        background-repeat: no-repeat;
        position: relative;
    }
    .tile{
        box-shadow: none !important;
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0px;
        text-align: center;
        padding-bottom: 20px;
        @include linearGradient(transparent 0%, rgba(0,0,0,0.8) 100%);
        padding-top: 10px;
        h2{
            font-weight: 900;
        }
        h2, p{
            color: #FFF;
            text-shadow: 1px 1px 3px rgba(0,0,0,0.6);
        }
    }
    .ngxcarouselPoint{
        position: absolute;
        bottom: 0;
        right: 20px;
        li{
            padding: 6px !important;
            background: #FFF !important;
            box-shadow: 0 0 3px 1px rgba(0,0,0,0.6) !important; 
            &.active{
                transform: scale(1.3) !important;
                background: $primary !important;
            }
        }
    }
}

.search-carousel{
    background: $blue;
    .heading{
        position: absolute;
        left: 10px;
        top: 10px;
        float: left;
        width: 180px;
        color: #FFF;
        h3{
            font-size: 0.875em;
            text-transform: uppercase;
        }
        p{
            font-size: 0.750em;
        }
    }
    .section{
        position: relative;
        padding: 10px;
        .item{
            padding: 0 5px;
            .tile{
                @include boxShadow(none);
                h3{
                    color: #FFF;
                    font-weight: 700;
                    font-size: 0.875em;
                }
                p{
                    color: #a9d3ff;
                    font-weight: 300;
                    font-size: 0.750em;
                }
            }
        }
    }
    .ngxcarousel{
        padding-left: 220px;
        position: relative;
        .leftRs, .rightRs{
            position: absolute;
            top: 50%;
            font-size: 2em;
            color: #FFF;
            margin-top: -24px;
        }
        .leftRs{
            left: 180px;
        }
        .rightRs{
            right: 0;
        }
    }
}

.events-festival{
    background-color: #E0E0E0;
    .nav-tabs,.nav-tabs:hover{
        border: none;
    }   
    .nav-tabs{
        .nav-link.active{
            background: #FFF;
            border: none;
        }
        .nav-item{
            margin-right: 3px;
            border-radius: 5px 5px 0 0;
            background: #DDD;
            margin-bottom: 0;
            @include boxShadow(0 -1px 6px rgba(0, 0, 0, 0.15) inset);
            a{
                color: #d97b1c;
            }
            &:hover, &.active{
                background: #FFF;
            }
        }
    }
    .nav-link, .nav-link:hover{
        padding: 10px 30px;
        border: none !important;
    }

    .event-tabs{
        position: relative;
        .choose-location{
            position: absolute;
            right: 0px;
            top:20px;
            cursor: pointer;
        }
        .tab-container{
            float: left;
            width: 100%;
            margin:10px 0; 
            padding:3px 0 6px 10px;
            border-radius: 4px;
            .tab-content {
                zoom: 1;
                background: #FFF;
                padding: 5px 7px;
            }
            .tab-content:before,
            .tab-content:after {
                content: "";
                display: table; 
            }
            .tab-content:after {
                clear: both; 
            }
        }
    }
    .featured-event{
        position: relative;
        .calendar-box{
            position: absolute;
            left: 0;
            top: 0;
            bottom: 0;
            width: 49%;
            background: #CCC;
            .btn-group{
                float: right;
                .btn{
                    padding: 3px 6px;
                    font-size: 0.675rem;
                }
            }
            h3{
                font-size: 1.125rem;
                font-weight: bold;
            }
        }
        .event-img{
            width:286px;
            height:240px;
            float: left;
            margin:5px 10px 5px 0;
            background-position:center ; 
            position: relative;
            overflow: hidden;
            &:first-child{
                width: 582px;
            }
        }
        .upcomingevent-box{
            margin-left: 50%;
            .event-img{
                &:first-child{
                    width: 286px;
                }       
            }
        }
        .margin-recm{
            margin-right: 0px !important;
        }
        .event-img h2 {
            font-weight: 900; 
        }
        .event-date{
            background: #FFF;
            position: absolute;
            right: 5px;
            top: 5px;
            border-radius: 3px;
            .date{
                float: left;
                font-size: 1.250em;
                font-weight: 900;
                padding: 7px 5px;
                line-height: 1.250rem;
                margin-right: 37px;
                color: #000;
                text-align: center;
            }
            .month-year{
                font-weight: 500;
                color: #FFF;
                background: $primary;
                padding: 5px;
                text-align: center;
                position: absolute;
                top: 0;
                right: 0;
                bottom: 0;
                border-radius: 0 3px 3px 0;
                .month{
                    font-size: 0.750em;
                    text-transform: uppercase;
                    font-weight: 900;
                    line-height: 0.750rem;
                }
                .year{
                    font-size: 0.675em;
                    line-height : 0.675rem;
                    margin-top: 1px;
                    color: #DDD;
                }
            }
        }
        .description {
            width: 100%;
            color: #FFF;
            padding:0 12px; 
            text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.6);
            position: absolute;
            bottom: -40px;
            left:0px;
            -moz-transition:all ease 1s;transition:all 1s ease;
            -webkit-transition:all 500ms ease;-ms-transition:all 500ms ease;-o-transition:all 500ms ease;
            background: transparent 0%;
            background: -moz-linear-gradient(top, transparent 0% 0%, rgba(0, 0, 0, 0.8) 100% 100%);
            background: -webkit-gradient(left top, left bottom, color-stop(0%, transparent 0%), color-stop(100%, rgba(0, 0, 0, 0.8) 100%));
            background: -webkit-linear-gradient(top, transparent 0% 0%, rgba(0, 0, 0, 0.8) 100% 100%);
            background: -o-linear-gradient(top, transparent 0% 0%, rgba(0, 0, 0, 0.8) 100% 100%);
            background: -ms-linear-gradient(top, transparent 0% 0%, rgba(0, 0, 0, 0.8) 100% 100%);
            background: linear-gradient(to bottom, transparent 0% 0%, rgba(0, 0, 0, 0.8) 100% 100%);
            h2{
                font-size: 0.85em;
            }
            .book-now{
                float: left;
                color: #fff;  
                padding:5px 12px;
                text-transform: uppercase;
                margin:10px 0px 10px;
                background: #f46603 none repeat scroll 0 0;
                border-radius: 3px;
                font-size: .75em;
                text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.6);
            }
            p{
                font-size: 0.75em;
                color: #e8f33e;
            }
        }
    }
    .event-img:hover .description{
        bottom:0px;
    }
}

.temple-donations{
    .temple_list{
        ul{
            background: #E0E0E0;
            float: right;
            width: 100%;
            max-width: 350px;
            max-height: 490px;
            overflow-y: auto;
            padding-left: 20px;
            @include scrollBar();
            li{
                border-bottom: 1px solid rgba(255,255,255,0.1);
                background: #333;
                position: relative;
                /*@include boxShadow(0 -1px 6px rgba(0, 0, 0, 0.35) inset);*/
                a{
                    padding: 5px;
                    display: block;
                    .img{
                        width: 120px;
                        height: 70px;
                        float: left;
                        margin-right: 10px;
                        background-size: cover;
                        background-repeat: no-repeat;
                        background-position: center center;
                    }
                    h4{
                        color: #FFF;
                    }
                    &:after{
                        content: " ";
                        display: table;
                        clear: both;
                    }
                }
                &:hover{
                    background: #666;
                }
                &.active{
                    background: $primary;
                    @include boxShadow(none);
                    &:after{
                        content: "";
                        position: absolute;
                        width: 0;
                        height: 0;
                        display: block;
                        border: 15px solid $primary;
                        border-left: 15px solid transparent;
                        border-top: 15px solid transparent;
                        border-bottom: 15px solid transparent;
                        left: -29px;
                        top: 50%;
                        margin-top: -15px;
                    }
                }
            }
        }
    }

    .temple_details{
        height: 490px;
        position: relative;
        margin-right: 350px;
        /*border: 5px solid $blue;*/
        &:hover{
            &:after{
                background: rgba(0,0,0,0.6);
            }
        }
        .temple_image{
            width: 50%;
            height: 100%;
            background-size: cover;
            background-position: center center;
            background-repeat: no-repeat;
            float: left;
        }
        .details{
            background:#e0e0e0;
            display: table-cell;
            width: 50%;
            height: 100%;
            float: right;
            padding: 30px;
            padding-right: 0;
            .content{
                position: relative;
                z-index: 2;
                h3{
                    font-weight: 700;
                }
                h3, p{
                    color: #000;
                }
                h3 + p{
                    margin-top: 10px;
                    line-height: normal;
                    font-size: 0.875em;
                    color: #444;
                }
                .needs{
                    margin-top: 30px;
                    h4{
                        color: #000;
                        font-weight: 700;
                        font-size: 1.1250rem;
                    }
                    ul{
                        list-style: none;
                        padding: 0;
                        margin: 0;
                        margin-top: 10px;
                        li{
                            color: #333;
                            margin-bottom: 5px;
                            font-size: 0.875em;
                            i{
                                margin-right: 5px;
                            }
                        }
                    }
                }
            }
            .btn{
                margin-top: 20px;
            }
        }
    }
}

/*Curated Experiences*/
.section-bg.curated{
    background: #bbdcff;
    .box-head{
        margin: 0;
    }
}


/*Book a pandit*/
.section-book-pandit{
    background: #E0E0E0;
    .pandit-list{
        .pandit-box{
            float: left;
            margin: 10px 0px;
            width: 20%;
            padding: 0 5px;
            &:first-child{
                padding-left: 0;
            }
            &:last-child{
                padding-right: 0;
            }
            div.pandit{
                background: #EEE;
                border-radius: 5px;
                position: relative;
                border-radius: 5px 5px 0 0;
                margin-bottom: 2px;
                h4{
                    font-weight: 700;
                }
                h5{
                    font-size: 0.675em;
                    display: block;
                    float: left;
                }
                .image{
                    min-height: 180px;
                    background-size: cover;
                    background-repeat: no-repeat;
                    background-position: center top;
                    border-radius: 5px 5px 0 0;
                }
                .meta{
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    right: 0;
                    padding: 5px 5px;
                    background: rgba(0,0,0,0.5);
                    color: #FFF;
                    @include animation();
                }
            }
            &:hover, &.active{
                cursor: pointer;
                div.pandit{
                    .meta{
                        background: #FFF;
                        color: #333;
                        &:after{
                                content: " ";
                                display: block;
                                border: 15px solid #FFF;
                                border-top: 20px solid transparent;
                                border-left: 20px solid transparent;
                                border-right: 20px solid transparent;
                                position: absolute;
                                bottom: -20px;
                                left: 50%;
                                margin-left: -30px;
                        }
                    }
                }
            }
        }
    }
    .pandit-detail{
        margin: 0px 0 10px;
        width:100%;
        background: #ffffff;
        position: relative;
        border: 2px solid #FFF;
        margin-top: 7px;
        .pandit-map-section{
            position: absolute;
            left: 0;
            top: 0;
            bottom: 0;
            width: 500px;
            z-index: 1;
            background: url(/assets/images/map.JPG) no-repeat center center;
            background-size: cover;
        }
        .pandit-desc-section{
            padding-left: 500px;
            min-height: 200px;
            width: 100%;
        }
    }
    .about-tab h3, .pooja-offered h3, .availability h3{
        margin:15px 0; 
    }
    .about-tab > div img{
        max-width: 100px;
        float: left;
        margin-right: 15px;
        margin-top: 4px;
    }
    .poojaList{
        margin:5px 0px;
        font-size: 0.875em;
        &  > div{
            float: left;
            width: 33%;
            margin-bottom: 10px
        }
    }

    .pandit-desc-section{
        .nav-tabs{
            overflow: hidden;
            list-style: none;    
            background-color: $primary;
            border-bottom: none;
            padding-left: 10px;
            li{
                float: left;
                display: block;
                color: black;
                text-align: center;
                text-decoration: none;
                font-size: 17px;
                a{
                    background: transparent;
                    border: none;
                    color: #FFF;
                    font-size: 0.875em;
                }
                &.active{
                    a{
                        color: #000;
                        border-bottom: 3px solid #000;
                        font-weight: 700;
                        text-shadow: 1px 1px 1px rgba(255,255,255,0.3);
                    }
                }
            }
        }
        .tab-content{
            border: none;
            overflow-y:auto;
            height:300px;
            padding: 5px 20px;
            .tab-heading{
                font-size: 1.125rem;
                font-weight: 700;
            }
            p{
                font-size: 0.875em;
            }
            .availability{
                .available-slot{
                    border-bottom: 1px solid #d2d3d4;
                    padding: 5px 0;    
                    clear: both;
                    float: left;
                    width: 100%;
                    .day-time{
                        float: left;
                        font-size: 0.875em;
                    }
                    .book-slot{
                        float: right;
                        button{
                            color: #fff;
                            border: none;
                            text-transform: uppercase;
                            margin:10px 0px 0px;
                            background: $primary;
                            font-size: 0.750em;
                        }
                    }
                }
            }
        }
    }
}

/*curated-experiences-section*/
.curated-experiences-section {
    float: left;
    width: 25%;
    text-align: center;
    .icon{
        margin-bottom: 20px;
    }
    .box-head h2{
        font-size: 1.272em;
