}


/****Calendar***/
.cal-month-view .cal-cell-top, .cal-month-view .cal-day-cell{
    min-height: initial;
}
.cal-month-view .cal-day-cell.cal-today .cal-day-number{
    font-size: 1.2em;
}
.ng-star-inserted{
    position: relative;
}
mwl-calendar-open-day-events{
    position: absolute;
    top:100%;
    left: 0;right: 0;
    z-index: 1;
}
.cal-month-view .cal-day-badge{
    position: absolute;
    margin-top: 3px;
    margin-left: 3px;
    padding: 1px 3px;
}

/*** BLOG ***/
.blog{
    ul{
        list-style: none;
        padding: 0;
        margin: 0 -20px;
        li{
            display: block;
            float: left;
            width: 25%;
            padding: 0 20px;
            img{
                max-width: 100%;
                border-radius: 4px;
            }
            h3{
                font-weight: 900;
                font-size: 1.125em;
                margin-top: 10px;
                color: $primary;
            }
            p{
                font-size: 0.875em;
                margin: 5px 0 0;
            }
            .meta{
                font-size: 0.675em;
                color: #8a8a8a;
                margin-top: 3px;
                .posted{
                    float: left;
                }
                .date{
                    float: right;
                }
            }
        }
    }
}

/**Feedback**/
.feedback{
    .tile{
        @include boxShadow(none !important);
    }
    .feedback-body{
        position: relative;
        img{
            position: absolute;
            max-width: 60px;
            height: auto;
            top: 20px;
        }
        .meta{
            padding: 20px;
            padding-left: 80px;
            h3{
                font-size: 1.225em;
                font-weight: 900;
                display: none;
            }
            p.comment{
                font-size: 1em;
                i.fa-quote-left{
                    margin-right: 10px;
                    color: $primary;
                }
                i.fa-quote-right{
                    margin-left: 10px;
                    color: $primary;
                }
            }
            .rating{
                float: none;
                text-align: left;
                margin-top: 5px;
            }
            h4{
                margin-top: 10px; 
            }
            .location{
                color: #888;
                font-size: 0.675em;
            }
        }
    }
}

.sidebarSection .item{
  width: 50%;
  padding: 5px 5px;
  float: left;
}
.sidebarSection .tile{
    
    display: inline-block;
    white-space: initial;    
    box-sizing: border-box;
    vertical-align: top;
    width: 100%;
    transition: transform .6s ease;
}
.sidebarSection,.temple-donations .main .contentSection{
  padding: 0px !important;
  background: none !important;
}
.sidebarSection .across-india{
    margin: 7px -5px;
}
.sidebarSection a .img {
    width: 100%;
    height: 107px;
    margin-right: 10px;
    border-radius: 5px 5px 0px 0;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}
.sidebarSection .detail{
    min-height:80px;
    background: #fff;
    font-size: 1em;
    color: #000000;
    font-weight:900;
    padding:10px; 
    line-height: 1.2em;
    border: 1px solid #e2e3e4; 
    border-top: none;
}
.sidebarSection .detail span{
  font-weight: 400;
  color: #999999;
  display: block;
  font-size: 0.675em;
}

/***Temple Donation***/
.temple-donations{
    background: #EEE;
    padding: 20px 0;
    .filterSection{  
        border: 1px solid #DDD;
        width: 200px;
        float: left;
        padding: 15px;
        background: #FFF;
        border-radius: 5px;
        h4{
            padding-bottom: 10px;
            margin-bottom: 10px;
            border-bottom: 1px solid #DDD;
            font-weight: bold;
        }
        .filterBox{
            h5{
                text-transform: uppercase;
                margin-top: 10px;
                font-weight: bold;
            }
            .filterList{
                list-style: none;
                padding: 0;
                margin: 5px 0 10px;
                padding-bottom: 15px;
                border-bottom: 1px solid #DDD;
                max-height: 180px;
                overflow-y: auto;
                @include scrollBar("#666");
                li{
                    margin-bottom: 3px;
                    label{
                        user-select: none;
                        cursor: pointer;
                        margin: 0;
                        font-size: 0.875rem;
                        display: block;
                    }
                }
            }
        }
    }
    .main{
        padding-left: 220px;
        .contentSection{
            float: left;
            background: #FFF;
            border-radius: 5px;
            width: calc(60% - 20px);
        }
        .sidebarSection{
            float: right;
            width: 40%;
            background: #FFF;
            border-radius: 5px;
            padding: 15px;
        }
    }
}

/***Single Page**/
.singlePage{
    padding: 20px 0;
    background: #EEE;
    .mainSection{
        padding-right: 412px;
        .mainSectionContent{
            h1{
                margin: 1rem 0 0;
                text-transform: capitalize;
                color: #000;
                & + p{
                    color: #999;
                    font-size: 0.875em;
                }
            }
            .mainBanner{
                background-repeat: no-repeat;
                background-size: cover;
                background-position: center center;
                min-height: 200px;
                border-radius: 5px 5px 0 0;
            }
        }
    }
    .sidebarSection{
        float: right;
        width: 100%;
        max-width: 392px;
        h4{
            text-transform: uppercase;
            font-size: 0.875rem;
        }
    }
}