.uil-ring-css {
  background: none;
  position: relative;
  width: 15px;
  height: 15px;
  display: inline-block;
}
.uil-ring-css > i {
  position: absolute;
  display: block;
  width: 15px;
  height: 15px;
  top: 3px;
  left: -5px;
  border-radius: 50%;
  box-shadow: 0 1px 0 0 #dbdbdb;
  -ms-animation: uil-ring-anim 0.4s linear infinite;
  -moz-animation: uil-ring-anim 0.4s linear infinite;
  -webkit-animation: uil-ring-anim 0.4s linear infinite;
  -o-animation: uil-ring-anim 0.4s linear infinite;
  animation: uil-ring-anim 0.4s linear infinite;
}