@mixin linearGradient($startColor, $stopColor){
	background: $startColor;
	background: -moz-linear-gradient(top, $startColor 0%, $stopColor 100%);
	background: -webkit-gradient(left top, left bottom, color-stop(0%, $startColor), color-stop(100%, $stopColor));
	background: -webkit-linear-gradient(top, $startColor 0%, $stopColor 100%);
	background: -o-linear-gradient(top, $startColor 0%, $stopColor 100%);
	background: -ms-linear-gradient(top, $startColor 0%, $stopColor 100%);
	background: linear-gradient(to bottom, $startColor 0%, $stopColor 100%);
}

@mixin radialGradient($startColor, $stopColor){
	background: $stopColor; /* Old browsers */
	background: -moz-radial-gradient(center, ellipse cover, $startColor 0%, $stopColor 100%); /* FF3.6-15 */
	background: -webkit-radial-gradient(center, ellipse cover, $startColor 0%, $stopColor 100%); /* Chrome10-25,Safari5.1-6 */
	background: radial-gradient(ellipse at center, $startColor 0%, $stopColor 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='$stopColor', endColorstr='$stopColor',GradientType=1 );
}

@mixin boxShadow($shadow){
	box-shadow: $shadow;
	-webkit-box-shadow: $shadow;
	-moz-box-shadow: $shadow;
	-o-box-shadow: $shadow;
	-ms-box-shadow: $shadow;
}

@mixin animation($property: all){
  transition: $property ease 500ms;
  -webkit-transition: $property ease 500ms;
  -moz-transition: $property ease 500ms;
  -o-transition: $property ease 500ms;
  -ms-transition: $property ease 500ms;
}

@mixin flexbox() {
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
}

// The 'flex' shorthand
// - applies to: flex items
// <positive-number>, initial, auto, or none
@mixin flex($values: 1) {
  -webkit-box-flex: $values;
     -moz-box-flex: $values;
      -webkit-flex: $values;
  	  -ms-flex: $values;
  	      flex: $values;
}

// Flex Flow Direction
// - applies to: flex containers
// row | row-reverse | column | column-reverse
@mixin flex-direction($direction) {
  -webkit-flex-direction: $direction;
     -moz-flex-direction: $direction;
      -ms-flex-direction: $direction;
          flex-direction: $direction;
}

// Flex Line Wrapping
// - applies to: flex containers
// nowrap | wrap | wrap-reverse
@mixin flex-wrap($wrap) {
  -webkit-flex-wrap: $wrap;
     -moz-flex-wrap: $wrap;
      -ms-flex-wrap: $wrap;
          flex-wrap: $wrap;
}

// Flex Direction and Wrap
// - applies to: flex containers
// <flex-direction> || <flex-wrap>
@mixin flex-flow($flow) {
  -webkit-flex-flow: $flow;
     -moz-flex-flow: $flow;
      -ms-flex-flow: $flow;
          flex-flow: $flow;
}

// Display Order
// - applies to: flex items
// <integer>
@mixin order($val) {
  -webkit-box-ordinal-group: $val;  
     -moz-box-ordinal-group: $val;     
  	     -ms-flex-order: $val;     
  	      -webkit-order: $val;  
  		      order: $val;
}

// Axis Alignment
// - applies to: flex containers
// flex-start | flex-end | center | space-between | space-around 
@mixin justify-content($justify) {
  -webkit-justify-content: $justify;
     -moz-justify-content: $justify;
      -ms-justify-content: $justify;
          justify-content: $justify;
            -ms-flex-pack: $justify;
}

// Packing Flex Lines
// - applies to: multi-line flex containers
// flex-start | flex-end | center | space-between | space-around | stretch 
@mixin align-content($align) {
  -webkit-align-content: $align;
     -moz-align-content: $align;
      -ms-align-content: $align;
          align-content: $align;
}

// Cross-axis Alignment
// - applies to: flex containers
// flex-start | flex-end | center | baseline | stretch 
@mixin align-items($align) {
  -webkit-align-items: $align;
     -moz-align-items: $align;
      -ms-align-items: $align;
          align-items: $align;
}

// Cross-axis Alignment
// - applies to: flex items
// auto | flex-start | flex-end | center | baseline | stretch 
@mixin align-self($align) {
  -webkit-align-self: $align;
     -moz-align-self: $align;
      -ms-align-self: $align;
          align-self: $align;
}

@mixin scrollBar($color: #000){
  &::-webkit-scrollbar-track{
      @include boxShadow(inset 0 0 6px rgba(0,0,0,0.3));
      border-radius: 5px;
      background-color: #F5F5F5;
  }
  &::-webkit-scrollbar{
      width: 5px;
      background-color: #F5F5F5;
  }

  &::-webkit-scrollbar-thumb{
      border-radius: 10px;
      @include boxShadow(inset 0 0 6px rgba(0,0,0,0.3));
      background-color: $color;
  }
}