@media screen and (max-width: 720px){
 body, html {
   font-size: 0.825em;
 }
 audio{
  width: 100%;
}
body {
  padding-top: 74px;
}
.events-festival .featured-event .event-img:first-child{
  width: 99.5%;
  margin-right: 0px;
  height: 130px;
}
.events-festival .featured-event .event-img{
  width: 49%;
  margin: 2px 1% 2px 0;
  height: 130px;
}
.events-festival .nav-link, .events-festival .nav-link:hover{
  padding:3px 5px;
  font-size: 0.925em;
}
.video-section,.audio-section{
  width: 100%;
  margin: 0px;
}
.banner-carousel .item{
  min-height: 300px;
}
.search-carousel .heading{
  width: 100px;
}
.search-carousel .ngxcarousel{
 padding-left: 110px;

}
.search-carousel .ngxcarousel .leftRs{
  left: 100px;
}
.search-carousel .ngxcarousel .leftRs, .search-carousel .ngxcarousel .rightRs{
  margin-top: -10px;
}
.sideNav-icon{
  display: block;
}
.navbar-brand img{
  max-width: 250px;
  margin: 0px;
}
.fixed-top{
  border-bottom: 3px solid #bf2d26;
}
.search-ico{
  display: block;
}
.primary-search,.user-detail,.hide-on-body,.topbar {
  display: none;
}
.mobile-menu{
  width:180px;
  position: fixed;
  display: block;
  padding: 0px;

}
.mobile-menu .nav-item{
  float: none;
  display: block;
  border-bottom: 1px solid #750500;
  width: 100%;
  line-height:40px;
}
.mobile-menu .nav-item:last-child{
  border-bottom:none;
}
.mobile-menu li.nav-item a{
  padding: 3px 7px;
  font-size: 1.5em;
}
.home-text{
  display: block;
}
.mobile-menu .fa-home,.temple_details, .sub-gallery .sub-gallery-1:nth-child(3), .sub-gallery .sub-gallery-2:nth-child(7), .sub-gallery .sub-gallery-2:nth-child(8),.pandit-detail,.event-img:nth-child(6),.event-img:nth-child(7),.download-app{
  display: none;
}
.banner-carousel .rightRs, .banner-carousel .leftRs{
  margin-top: -14px;
}
.banner-carousel .ngxcarouselPoint li{
  padding: 3px !important;
}
.banner-carousel .ngxcarouselPoint{
  right: 10px;
  padding: 4px !important;
}
.main-gallery{
  width: 35%;
  height: 162px;
}
.sub-gallery{
  width: 65%;
}
.sub-gallery .sub-gallery-1{
  width: calc(50% - 1px);
  height: 103px;
}
.sub-gallery .sub-gallery-2{
  width: calc(33.33% - 1px);
  height: 58px;
}
.temple-donations .temple_list ul{
  max-height: 290px;
}
.curated-experiences-section{
  width: 50%;
  height: 120px;
  margin:10px 0;
}
.section-bg{
  background-size: 50px 50px;
  padding:10px 0;
}
.box-head{
  margin: 0px;
}
.box-head h2{
  padding: 15px 0;
}
.events-festival .event-tabs .tab-container{
  padding:0 2px;
  margin: 0px
}
.audio-video .audio{
  height: 290px;
}
.section-footer .download-section > div,.about-religios-india{
  width:96%;
  margin:0 2% 10px;
}
.curated-experiences-section .box-head h2{
  padding: 0 0 7px;
  font-size: 1em;
}
.curated-experiences-section .icon{
  margin-bottom: 0px;
}
.pandit-box:last-child{
  display: block;
}
.section-book-pandit .pandit-list .pandit-box{
  width: 33.33%;
  padding:0 5px 0 0;
}
.section-book-pandit .pandit-list .pandit-box div.pandit .image{
  min-height: 110px;

}
.pandit-list{
  width: 98%;
  margin:0px 1%;
}
.audio-video h4{
  padding: 7px;
  border-bottom: 1px solid;
  margin: 10px 3px;
}
}